import React from 'react';

function E404() {
  const source = 'react'; // Puedes cambiar esto seg√∫n sea necesario

  return (
    <iframe
      src={`/404.html?source=${encodeURIComponent(source)}`}
      title="Archivo HTML"
      width="100%"
      height="600px"
      style={{ border: 'none' }}
    />
  );
}

export default E404;
